/**
 * @file Simple Demand Schema Tests - without complex imports
 * @description Simplified test to isolate the hanging issue
 */

import { describe, it, expect } from '@jest/globals';
import { z } from 'zod';

// Simple inline schema for testing
const testDemandCommentSchema = z.object({
  demandId: z.string().uuid(),
  commenterId: z.string().uuid(),
  commentText: z.string().min(1).max(1000),
});

describe('Simple Demand Comment Schema Tests', () => {
  it('should validate a basic comment', () => {
    const validComment = {
      demandId: '123e4567-e89b-12d3-a456-426614174000',
      commenterId: '987fcdeb-51a2-43d1-9f32-123456789abc',
      commentText: 'This is a valid comment',
    };

    expect(() => testDemandCommentSchema.parse(validComment)).not.toThrow();
  });

  it('should reject invalid UUID', () => {
    const invalidComment = {
      demandId: 'invalid-uuid',
      commenterId: '987fcdeb-51a2-43d1-9f32-123456789abc',
      commentText: 'Valid comment text',
    };

    expect(() => testDemandCommentSchema.parse(invalidComment)).toThrow();
  });
});